<template>
  <div>
    <div style="display: flex; justify-content: center;">
      <span class="jaffee" style="background-image: url('~@/assets/images/jaffee.png'); ">
        <span class="a"></span>
        <span class="bc">
          <span class="b"></span>
          <span class="c"></span>
        </span>
        <img src="~@/assets/images/jaffee.png">
      </span>
    </div>
    <div style="display: flex; justify-content: center; font-size: 22px;font-weight: 500; margin-bottom: 20px; border-bottom: 2px dashed #d7d7d7">CSS脑洞例子</div>
    <div class="ad-banner" id="move">
      <img @mousemove="changeRotate" @mouseleave="initRotate" src="~@/assets/images/css-demo-1.jpeg" alt="动图" style="height: 250px;border-radius: 10px;">
    </div>
    <v-md-editor :value="text" mode="preview"></v-md-editor>
    <div style="margin: -20px 0 40px; border-bottom: 2px dashed #d7d7d7"></div>

    <div class="head">
      <img src="~@/assets/images/av-head.png" alt="a random avatar picture" class="head-img">
    </div>
    <v-md-editor :value="text2" mode="preview"></v-md-editor>
    <div style="margin: -20px 0 60px; border-bottom: 2px dashed #d7d7d7"></div>
    <div style="margin-top: -20px; display: flex; justify-content: center">
      <div class="container-img" @mousemove="magnifierMousemove">
        <div class="magnifier"></div>
      </div>
    </div>
    <v-md-editor :value="text3" mode="preview"></v-md-editor>
  </div>
</template>

<script>
export default {
  name: "cssDemo",
  data() {
    return {
      text: `
  > 复刻-顾家家居官网样式，宣传图带悬浮按压效果

  html代码：
  \`\`\`html
  <div class="ad-banner" id="move">
    <img @mousemove="changeRotate" @mouseleave="initRotate" src="~@/assets/images/css-demo-1.jpeg" alt="动图" style="height: 250px;border-radius: 10px;">
  </div>
  \`\`\`
  js代码：
  \`\`\`js
  changeRotate(e) {
    // offsetX,offsetY 以左上角为原点的偏移量，要固定好width方便计算，这里原图是1220 * 500
    let rotateDeg = 12; // 旋转角度
    let halfWidth = 305; // 设置图片宽度的一半。例如这里原图是1220，img高度取了一半250，所以对应图片宽是610，取这个的一半就是305；
    let imgHeight = 250 // 设置图片高度
    let y = 0;
    let x = 0;
    // console.log('offsetX:',e.offsetX,'offsetY:',e.offsetY);
    if (e.offsetX > halfWidth) {
      y = ((e.offsetX - halfWidth) / halfWidth) * rotateDeg;
    } else {
      y = ((halfWidth - e.offsetX) / halfWidth) * -rotateDeg;
    }
    x = ( e.offsetY / imgHeight) * -rotateDeg;
    // console.log('y:',y,'x:',x);
    document.querySelector('#move').style.cssText = \`transform: rotateY(\${y}deg) rotateX(\${x}deg);\`;
  },
  initRotate() {
    document.querySelector('#move').style.cssText = \`transform: rotateY(0deg) rotateX(0deg);\`;
  },
  \`\`\`
  css代码：
  \`\`\`css
  .ad-banner {
    position: relative;
    display: flex;
    justify-content: center;
    /*设置旋转中心位置*/
    transform-origin: 50% 50%;
    transform-style: preserve-3d;
    transform: rotateY(0deg) rotateX(0deg);
    transition-duration: 0.3s;
    transition-timing-function: linear;
  }
  \`\`\`
      `,
      text2: `
  > 头像立体放大效果

  html代码：
  \`\`\`html
   <div class="head">
    <img src="https://assets.codepen.io/1480814/av+1.png" alt="a random avatar picture" class="head-img">
  </div>
  \`\`\`
  css代码：
  \`\`\`css
  .head {
    margin: -40px 0 0 0;
    min-height: 40vh;
    display: grid;
    place-content: center;
    background: #E0E4CC;
  }
  .head-img {
    --s: 280px; /* image size */
    --b: 5px; /* border thickness */
    --c: #C02942; /* border color */
    --f: 1; /* initial scale */

    width: var(--s);
    /*aspect-ratio: 1;*/
    padding-top: calc(var(--s)/5);
    cursor: pointer;
    border-radius: 0 0 999px 999px;
    --_g: 50%/calc(100%/var(--f)) 100% no-repeat content-box;
    --_o: calc((1/var(--f) - 1)*var(--s)/2 - var(--b));
    outline: var(--b) solid var(--c);
    outline-offset: var(--_o);
    background:
        radial-gradient(
            circle closest-side,
            #ECD078 calc(99% - var(--b)),var(--c) calc(100% - var(--b)) 99%,#0000
        ) var(--_g);
    -webkit-mask:
        linear-gradient(#000 0 0) no-repeat
        50% calc(1px - var(--_o)) / calc(100%/var(--f) - 2*var(--b) - 2px) 50%,
        radial-gradient(circle closest-side,#000 99%,#0000) var(--_g);
    transform: scale(var(--f));
    transition: .5s;
  }
  .head-img:hover {
    --f: 1.35; /* hover scale */
  }
  \`\`\`
      `,
      // 放大镜属性
      magnifier_width: 80,
      magnifier_height: 80,
      container: {},
      magnifier: {},
      text3: `
  > 类似淘宝购物，衣服放大镜效果
  > 原文链接：https://css-tricks.com/moving-backgrounds/

  html代码：
  \`\`\`html
  <div class="container-img" @mousemove="magnifierMousemove">
    <div class="magnifier"></div>
  </div>
  \`\`\`
  js代码：
  \`\`\`js
  data() {
    return {
      magnifier_width: 80,
      magnifier_height: 80,
      container: {},
      magnifier: {},
    };
  },
  methods: {
    magnifierMousemove(e) {
      // 获取鼠标位置
      // 获得container元素的左，上，右和下分别相对浏览器视窗的位置。rect.left就是元素左边到视窗左边的距离；rectObject.width：是元素自身的宽
      let rect = this.container.getBoundingClientRect();
      let x = e.pageX - rect.left; // e.pageX 相对于文档的定位，文档的左上角为(0,0)
      let y = e.pageY - rect.top;
      // x，y是当前鼠标距离图片左上角的距离
      x = x - window.pageXOffset; // pageXOffset 文档已经从窗口左上角水平滚动的像素数
      y = y - window.pageYOffset; // pageYOffset 文档已经从窗口左上角垂直滚动的像素数
      // 防止放大镜离开容器
      // 设置放大镜距离顶部和左侧值
      if (x >= 0 && x <= this.container.clientWidth - this.magnifier_width) {
        this.magnifier.style.left = x + "px";
      }
      if (y >= 0 && y <= this.container.clientHeight - this.magnifier_height) {
        this.magnifier.style.top = y + "px";
      }
      // 设置方法倍数，长宽要和图片一样，这样位置才对应上
      const magnify = 2;
      const imgWidth = 610;
      const imgHeight = 250;
      this.magnifier.style.backgroundSize = imgWidth * magnify + "px " + imgHeight * magnify + "px";
      // 放大镜div位置
      let magnify_x = x * magnify + 15;
      let magnify_y = y * magnify + 15;
      if (
          x <= this.container.clientWidth - this.magnifier_width &&
          y <= this.container.clientHeight - this.magnifier_height
      ) {
        this.magnifier.style.backgroundPosition = -magnify_x + "px " + -magnify_y + "px";
      }
    },
  },
  mounted() {
    this.container =  document.querySelector(".container-img");
    this.magnifier = document.querySelector(".magnifier");
  }
  \`\`\`
  css代码：
  \`\`\`css
  .container-img {
     height: 250px;
     width: 610px;
     background-size: cover;
     background-image: url("~@/assets/images/css-demo-1.jpeg");
     background-repeat: no-repeat;
     position: relative;
   }
  .magnifier {
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 4px;
    border: 2px solid #efefef;
    cursor: none;
    background-image: url("~@/assets/images/css-demo-1.jpeg");
    background-repeat: no-repeat;
  }
  \`\`\`
      `,
    };
  },
  methods: {
    // banner效果
    changeRotate(e) {
      // offsetX,offsetY 以左上角为原点的偏移量，要固定好width方便计算，这里原图是1220 * 500
      let rotateDeg = 12; // 旋转角度
      let halfWidth = 305; // 设置图片宽度的一半。例如这里原图是1220，img高度取了一半250，所以对应图片宽是610，取这个的一半就是305；
      let imgHeight = 250 // 设置图片高度
      let y = 0;
      let x = 0;
      // console.log('offsetX:',e.offsetX,'offsetY:',e.offsetY);
      if (e.offsetX > halfWidth) {
        y = ((e.offsetX - halfWidth) / halfWidth) * rotateDeg;
      } else {
        y = ((halfWidth - e.offsetX) / halfWidth) * -rotateDeg;
      }
      x = ( e.offsetY / imgHeight) * -rotateDeg;
      // console.log('y:',y,'x:',x);
      document.querySelector('#move').style.cssText = `transform: rotateY(${y}deg) rotateX(${x}deg);`;
    },
    initRotate() {
      document.querySelector('#move').style.cssText = `transform: rotateY(0deg) rotateX(0deg);`;
    },
    // 放大镜效果
    magnifierMousemove(e) {
      // 获取鼠标位置
      // 获得container元素的左，上，右和下分别相对浏览器视窗的位置。rect.left就是元素左边到视窗左边的距离；rectObject.width：是元素自身的宽
      let rect = this.container.getBoundingClientRect();
      let x = e.pageX - rect.left; // e.pageX 相对于文档的定位，文档的左上角为(0,0)
      let y = e.pageY - rect.top;
      // x，y是当前鼠标距离图片左上角的距离
      x = x - window.pageXOffset; // pageXOffset 文档已经从窗口左上角水平滚动的像素数
      y = y - window.pageYOffset; // pageYOffset 文档已经从窗口左上角垂直滚动的像素数
      // 防止放大镜离开容器
      // 设置放大镜距离顶部和左侧值
      if (x >= 0 && x <= this.container.clientWidth - this.magnifier_width) {
        this.magnifier.style.left = x + "px";
      }
      if (y >= 0 && y <= this.container.clientHeight - this.magnifier_height) {
        this.magnifier.style.top = y + "px";
      }
      // 设置方法倍数，长宽要和图片一样，这样位置才对应上
      const magnify = 2;
      const imgWidth = 610;
      const imgHeight = 250;
      this.magnifier.style.backgroundSize = imgWidth * magnify + "px " + imgHeight * magnify + "px";
      // 放大镜div位置
      let magnify_x = x * magnify + 15;
      let magnify_y = y * magnify + 15;
      if (
          x <= this.container.clientWidth - this.magnifier_width &&
          y <= this.container.clientHeight - this.magnifier_height
      ) {
        this.magnifier.style.backgroundPosition = -magnify_x + "px " + -magnify_y + "px";
      }
    },
  },
  mounted() {
    this.container =  document.querySelector(".container-img");
    this.magnifier = document.querySelector(".magnifier");
  }
}
</script>

